import React from 'react';
import { connect } from 'react-redux';
import { login } from 'redux/actions/Login/';
import { Translate } from "react-localize-redux";

class LoginPopupLayout extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
          userName: "",
          password: ""
        }
    }

    render() {
        if (this.props.authenKey) return <div></div>
        return (
            <div id="login-popup" className="fixed-top">
                <div className="container">
                    <div className="input-section">
                        <input
                            type="text"
                            className="username-input"
                            placeholder="Username"
                            onChange={(e) => {this.onUserNameChange(e.target.value)}}
                        />
                        <input
                            type="password"
                            className="passowrd-input"
                            placeholder="Password"
                            onChange={(e) => {this.onPasswordChange(e.target.value)}}
                        />

                        <button
                            className="btn login-button"
                            type="button"
                            onClick={()=> {
                              this.props.login(this.state)
                            }}>
                            <Translate id="Login"/>
                        </button>
                    </div>

                    <div
                      id="retrieve-login"
                      onClick={() => {
                          window.location = "https://member.liveguess.net/forget-password.html";
                      }}>
                        <Translate id="Forget password"/>
                    </div>

                </div>
            </div>
        )

    }

    onCaptchaChange(value) {
        this.setState({
            ...this.state,
            captcha: value
        })
    }

    onUserNameChange(userName) {
        this.setState({
            ...this.state,
            userName: userName
        })
    }

    onPasswordChange(password) {
        this.setState({
            ...this.state,
            password: password
        })
    }
}


const mapStateToProps = (state) => {
    return {
        authenKey: state.login.authenKey
    }
};

const mapDispatchToProps = (dispatch) => {
    return {
        login: (info) => dispatch(login(info))
    }
};

const LoginPopup = connect(
    mapStateToProps,
    mapDispatchToProps
)(LoginPopupLayout);

export default LoginPopup;
