import React from 'react';

class BetHistorySingle extends React.Component {
    constructor(props) {
        super(props)
    }

    render() {
        return (
          this.props.bets.map((bet, i) => {
            const { draw, content, value, winBet, gameEnd, guess, last } = bet;
            return (
              <div className="bet-single" key={i}>
                <div className="bet-result">{draw}</div>
                <div className="bet-content">
                  <div className="flickering">Last: {last}</div>
                  <div className="flickering">Guess: {guess}</div>
                </div>
                <div className="bet-content">
                  <div>
                    {content}
                  </div>
                  <div className="bet-time">
                    {gameEnd}
                  </div>
                </div>
                <div style={{display: "flex", alignItems: "center", justifyContent: "space-between"}}>
                  <input
                    type="number"
                    className="bet-amount"
                    value={value}
                    placeholder="0"
                    disabled={this.props.disabled}
                    onChange={(e) => { this.handleChange(e.target.value, bet) }}
                    required={true} />
                  <label style={winBet >= 0 ? {color: "green"} : {color: "red"}}>
                    {winBet}
                  </label>
                </div>
              </div>
            )
          }));
      }
}

export default BetHistorySingle;
