import React from 'react';
import ScrollUp from 'components/common/ScrollUp';
import { Translate } from "react-localize-redux";
import { withRouter } from 'react-router-dom';

class Footer extends React.Component {

  render() {
    return (
      <footer className="footer" id="footer">
        <ScrollUp scrollStepInPx="50" delayInMs="16.66" />
        <div className="container">
          <div className="content text-center text-md-left">
            <div className="row">
              <div className="col-sm-5">
                {this.renderLeftColumn()}
              </div>
              <div className="col-sm-7">
                {this.renderRightColumn()}
              </div>
            </div>
          </div>
        </div>
      </footer>
    );
  }

  renderLeftColumn() {
    return (
      <div className="container">
        <div className="row" style={{ marginBottom: "25px" }}>
          <span style={{ color: "#fbff47" }}>24 Feb 21:05 VN</span> |  2019-2050 GL (International) Ltd
        </div>
        <div className="row" style={{ justifyContent: "space-between" }}>
          <div style={{ flex: 1, paddingRight: "20px" }}>
            <ul className="list-unstyled" style={{ textAlign: "left" }}>
              <li>
                <a onClick={() => { this.props.history.push('/about/term-condition')}} ><Translate id="Terms and Condition"/></a>
              </li>
              <li>
                <a onClick={() => { this.props.history.push('/about/rules')}}><Translate id="Rules"/></a>
              </li>
              <li>
                <a onClick={() => { this.props.history.push('/about/contacts')}}><Translate id="Contact Us"/></a>
              </li>
              <li>
                <a onClick={() => { this.props.history.push('/about/feedback')}}><Translate id="Your Feedback"/></a>
              </li>
              <li>
                <a onClick={() => { this.props.history.push('/about/jobs')}}><Translate id="Jobs & Career"/></a>
              </li>
              <li>
                <a onClick={() => { this.props.history.push('/about/affiliate')}}><Translate id="Afiliate Program"/></a>
              </li>
            </ul>
          </div>
          <div style={{ flex: 1, paddingLeft: "20px" }}>
            <ul className="list-unstyled" style={{ textAlign: "left" }}>
              <li>
                <a onClick={() => { this.props.history.push('/about/policy')}}><Translate id="Privacy Policy" /></a>
              </li>
              <li>
                <a onClick={() => { this.props.history.push('/about/help')}}><Translate id="Help" /></a>
              </li>
              <li>
                <a onClick={() => { this.props.history.push('/about/office')}}><Translate id="Office" /></a>
              </li>
              <li>
                <a onClick={() => { this.props.history.push('/about/shop')}}><Translate id="Shop Locator" /></a>
              </li>
              <li>
                <a onClick={() => { this.props.history.push('/about/about')}}><Translate id="About" /></a>
              </li>
            </ul>
          </div>
        </div>
      </div>
    );
  }

  renderRightColumn() {
    return (
      <div className="container">
        <div className="row" style={{ textAlign: "left" }}>
          <Translate id="footer-first-verse"/>
        </div>
        <div className="row" style={{ textAlign: "left" }}>
          <Translate id="footer-second-verse"/>
        </div>
      </div>
    );
  }
}

export default withRouter(Footer);
