                <p>18.3 Any monies which are credited to Your Account, or paid to You as a result of an Error shall be deemed, pending resolution under paragraph 18.2, to be held by You on trust for us and shall be immediately repaid to us when a demand for payment is made by us to You. Where such circumstances exist, if You have monies in Your Account we may reclaim these monies from Your Account pursuant to paragraph 7.4. We agree that we shall use all reasonable endeavours to detect any Errors and inform You of them as soon as reasonably practicable.</p>

