import React from 'react';

const ContactPage = () => {
  return <div className="row">
    {staticContent}
    </div>
};

export default ContactPage;

const staticContent = <main className="col-sm-12">

    <article className="panel article">
        <div className="panel-heading">
            <h1 className="panel-title">How to contact Live Guess</h1>
        </div>

        <div className="panel-body">
            <div id="rn_whAnswerContentDisplay_8" className="rn_whAnswerContentDisplay rn_Output">
                <p><span style={{fontWeight: "bold"}}><a id="cs for betting shops" name="cs for betting shops"></a>Customer services for our betting shops</span></p>

                <p>Our customer relations team is here to help you with any queries you have about our betting shops. They are available on <span style={{fontWeight: "bold"}}>0870 518 1715 <span style={{fontWeight: "normal"}}>(calls to this number are charged at 1p a minute</span><span style={{fontWeight: "normal"}}>)</span></span>&nbsp;from 09:30 to 21:30 Monday to Friday, 08:30 to 21:30 on Saturdays and 11:00 to 18:00 on Sundays.</p>

                <p>You can also email <a href="mailto:customerhelp@williamhill.co.uk" target="_new">customerhelp@williamhill.co.uk</a>, or if you would like to send a letter the address is:</p>

                <p>Greenside House
                    <br/> 50 Station Road
                    <br/> Wood Green
                    <br/> London N22 7TP</p>
            </div>
        </div>

    </article>

</main>;
