import { combineReducers } from 'redux';

import { localizeReducer } from "react-localize-redux";

import {
  login,
  betReducer,
  fetchCategory,
  listHistoryBetClick,
  fetchBet
} from './reducer';

export default combineReducers({
  login,
  betReducer,
  fetchCategory,
  listHistoryBetClick,
  fetchBet,
  localize: localizeReducer
});
